@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Italianno&display=swap);
html {
  scroll-behavior: smooth;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "Karla", sans-serif;
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#959595+0,0d0d0d+46,010101+50,0a0a0a+53,4e4e4e+76,383838+87,1b1b1b+100;Black+Gloss+Pipe */
  background-image: linear-gradient(
    -225deg,
    #c47a19 30%,
    #ac5533 70%,
    #ffffff 100%
  );
  /* Add the blur effect */

  /* Full height */
  height: 100%;

  /* Center and scale the image nicely */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.logo {
    width: 75%;
  }
  
  .nav-theme {
    background-color: #7c5314f8;
    font-size: 20px;
  }

  .animate-navbar {
    box-shadow: 1px 1px 1px #222;
    -webkit-animation: moveDown 0.5s ease-in-out;
            animation: moveDown 0.5s ease-in-out;
  }
  
  @-webkit-keyframes moveDown {
    from {
      -webkit-transform: translateY(-5rem);
              transform: translateY(-5rem);
    }
    to {
      -webkit-transform: translateY(0rem);
              transform: translateY(0rem);
    }
  }
  
  @keyframes moveDown {
    from {
      -webkit-transform: translateY(-5rem);
              transform: translateY(-5rem);
    }
    to {
      -webkit-transform: translateY(0rem);
              transform: translateY(0rem);
    }
  }
.custom-img {
    height: 100vh;
    object-fit: cover;
  }
.accordian-main {
    cursor: pointer;
    display: inline-block;
    -webkit-text-decoration: none;
    text-decoration: none;
    font-size: 14px;
    background-color: rgb(110, 64, 12);
    color:rgb(110, 64, 12);
    border: none;
    border-radius: 4px;
  
    padding: 10px;
    color: #fff;
  }
  
  .list-styles {
    list-style-type: circle;
  }
  
  .image-style1 {
    width: 3em;
  }
h1 {
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-size: 12px;
}

.fas fa-paint-brush {
    font-size: 5px;
}


.temp {
	border: 2px solid black;
	display: block;
	padding: 10px;
}

.App {
	text-align: center;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		-webkit-animation: App-logo-spin infinite 20s linear;
		        animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #ec9c41;
}

.top-padding {
	padding-top: 120px;
}

@-webkit-keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

